}

.project-slide {
    padding: 2rem;
    max-width: 40rem;
    display: flex;
    width: 28rem;
    height: auto;
    margin: 3rem 5rem;
    border: 2px solid #f5cb86;
}

.project-slide:hover {
    transition: 0.3s ease-in;
    border-radius: 35px;
    background: linear-gradient(145deg, #15161a, #191a1f);
    

}

.project-slide:hover {
    /* color: #f5cb86; */
    transition: all 0.5s;
    /* transform: translate(0, 0.2em); */
    
    border-radius: 35px;
    background: linear-gradient(145deg, #15161a, #191a1f);
    box-shadow:
    35px 35px 70px #141419,
        -35px -35px 70px #1a1c21, ;
  }
  
  .project-slide:not(hover) {
    transition: all 0.5s;
  }


.project-slide .head {
    font-size: 1rem;
}

.project-slide .subhead {
    font-size: 2rem;
    margin: 0;
}


.project-slide p {
    opacity: 70%;
}

.project-slide p:hover {
    opacity: 100%;
}


