.icon-link {
    text-decoration: none;
    /* Remove default underline for the link */
}

.icon-row {
    display: flex;
    justify-content: center;
}

.icon-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    /* Adjust the height and width as needed */
    width: 48px;
}

.icon-background {

    /* background-color: #fff; Twitter blue color, change as needed */
    border-radius: 90%;
    /* Creates a circular background */
    padding: 8px;
    /* Adjust the padding as needed */
}

.twitter-icon {

    color: #f5cb86;
    /* Icon color, change as needed */
}




.contact-section p {
    opacity: 60%;
