}

.form-div {
    padding: 2rem;
    margin: 2rem;
    position: absolute;
    right: 0;
    /* height: auto; */
    width: 40vw;

    border-radius: 50px;
    background: #17181d;
    box-shadow: 35px 35px 70px #141419,
        -35px -35px 70px #1a1c21;
    /* box-shadow: 18px 18px 18px 18px rgba(0, 0, 0, 0.5); */
}

