button {
    margin-top: 6px;
    padding: 0.1em 0.25em;
    width: 28em;
    height: 3.7em;
    background-color: #17181d;
    border: 0.08em solid #fff;
    border-radius: 0.3em;
    font-size: 12px;
    margin: 2rem 0;
}

button span {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    bottom: 0.4em;
    width: 18.25em;
    height: 2.1em;
    background-color: #17181d;
    border-radius: 0.2em;
    font-size: 1.5em;
    color: #fff;
    border: 0.08em solid #fff;
    box-shadow: 0 0.4em 0.1em 0.019em #fff;
}

button span:hover {
    transition: all 0.5s;
    transform: translate(0, 0.4em);
    box-shadow: 0 0 0 0 #fff;
}

button span:not(hover) {
    transition: all 1s;
}







.form input {
    background-color: transparent;
    width: 22rem;
    padding: 0.7rem 1rem;
    border: hidden;
    /* margin: 0.7rem 1rem; */

    /* display: inline-block; */
    /* box-sizing: border-box; */
    /* width: 100%; */
    /* display: table-cell; */
    /* vertical-align: middle; */
}

.form input::after {
    content: '';
    width: 0%;
    height: 3px;
    background: #f5cb86;
    position: absolute;
    left: 0;
    bottom: 0.5rem;
}

